.info{
    text-align: center;
    padding-top: 47px;
}

.info__h2{
    visibility: hidden;
}

.info__h2Active{
    overflow: hidden;
    width: 7ch;
    display: inline-block;
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    white-space: nowrap;
    animation: flashing-border 1s ease infinite, printed-text 5s ease
}

.info__description{
    margin: 0 auto;
    margin-top: 38px;
    max-width: 745px;
    text-align: center;
    color: #fff;
    font-size: 18px;
    font-weight: 400;
}

.info__list{
    padding-top: 38px;
    display: flex;
    justify-content: space-around;
    gap: 50px;
}

.info__list li{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.info__span{
    position: relative;
    display: block;
    height: 62px;
    width: 62px;
    background-color: #212121;
    border-radius: 50%;
}

.info__span::after{
    content: "";
    display: block;
    position: absolute;
    left: 50%;
    top: 50%;
    height: 10px;
    width: 10px;
    background-color: #27AE60;
    border-radius: 50%;
    transform: translate(-50%, -50%);
}

.info__contact{
    margin-top: 18px;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
}

.contact{
    color: #fff;
    font-size: 18px;
    font-weight: 400;
}


@keyframes flashing-border{
    0%{
        border-right: 4px solid #4F4F4F;
    }
    50%{
        border-right: 4px solid transparent;
    }
    100%{
        border-right: 4px solid #4F4F4F;
    }
}

@keyframes printed-text {
    from{
        width: 0%;
    }

}

@media (max-width:950px ) {
    .info__list{
        flex-wrap: wrap;
    }

    .info__list li{
        width: 45%;
    }
}

@media (max-width: 768px) {
    .info{
        padding-top: 25px;
    }

    .info__h2Active{
        font-size: 25px;
    }

    .info__description{
        margin-top: 25px;
        font-size: 16px;
    }

    .info__span{
        height: 50px;
        width: 50px;
    }

    .info__contact{
        font-size: 15px;
    }

    .contact{
        font-size: 12px;
    }
}