.header{
    position: fixed;
    width: 100%;
    top: -1px;
    left: 0;
    z-index: 50;
}

.header::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #171717;
    z-index: 2;
}

.header__container{
    max-width: 1333px;
    margin: 0 auto;
    padding: 0px 20px 0px 0px;
}

.header__body{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
}

.header__logo{
    transition: all 0.3s ease 0s;
    overflow: hidden;
    flex: 0 0 200px;
    position: relative;
    z-index: 3;
}

.header__logo img{
    max-width: 100%;
    height: auto;
    display: block;
}

.header__burger{
    display: none;
}

.header__list{
    display: flex;
    gap: 40px;
    position: relative;
    z-index: 2;
}

.header__li{
    transition: all 0.3s ease 0s;
    color: #fff;
    font-size: 18px;
    font-weight: 400;
    border-bottom: 1px solid #171717;
}

.header__li:hover{
    border-bottom: 1px solid #fff;
}

@media (max-width:767px) {
    .header__body{
        height: 50px;
    }

    .header__logo{
        transition: all 0.3s ease 0s;
        flex: 0 0 150px;
    }

    .header__burger{
        display: block;
        position: relative;
        width: 30px;
        height: 20px;
        z-index: 3;
    }

    .header__burger span{
        position: absolute;
        background-color: #fff;
        left: 0;
        width: 100%;
        height: 2px;
        top: 9px;
        transition: all 0.3s ease 0s;
    }

    .header__burger::before,
    .header__burger::after{
        content: "";
        background-color: #fff;
        position: absolute;
        width: 100%;
        height: 2px;
        left: 0;
        transition: all 0.3s ease 0s;
    }

    .header__burger::before{
        top: 0;       
    }
    .header__burger::after{
        bottom: 0;
    }

    .header__burger.active::before{
        transform: rotate(45deg);
        top: 9px;
    }

    .header__burger.active::after{
        transform: rotate(-45deg);
        bottom: 9px;
    }

    .header__burger.active span{
        transform: scale(0);
    }

    .header__menu{
        position: fixed;
        top: -100%;
        left: 0;
        width: 100%;
        height: auto;
        overflow: auto;
        background-color: #171717;
        padding: 70px 10px 20px 10px;
        transition: all 0.3s ease 0s;
    }

    .header__menu.active{
        top: 0;
    }

    .header__list{
        display: block;
    }

    .header__list li{
        margin: 0px 0px 20px 0px;
    }
}