.main__body{
    margin-top: 124px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.main__info{
    display: flex;
    flex-direction: column;
    max-width: 358px;
}

.main__h2{
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    overflow: hidden;
}


.main__p{
    color: #828282;
    font-size: 18px;
    font-weight: 500;
    padding-top: 9px;
}

.main__btn{
    display: flex;
    gap: 10px;
    padding-top: 35px;
}

.btn_cv{
    transition: all 0.4s ease 0s;
    cursor: pointer;
    padding: 12px 27px;
    background-color: #27AE60;
    color: #fff;
    font-size: 14px;
    font-weight: 500;
}

.btn_cv:hover{
    background-color: #1a7540;
}

.btn_writeMe{
    transition: all 0.4s ease 0s;
    cursor: pointer;
    padding: 11px 34px;
    color: #fff;
    font-size: 14px;
    font-weight: 500;
    border: 1px solid #F2F2F2;
}

.btn_writeMe:hover{
    color: #181818;
    background-color: #fff;
}

.main__img {
    transition: all 0.3s ease 0s;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    max-width: 650px; 
    height: auto; 
    border-radius: 50%;
    overflow: hidden; 
  }
  
  .imgContainer {
    background-color: #181818;
    width: 100%;
    padding-bottom: 100%; 
    position: relative;
  }
  
  .img {
    padding: 20px;
    border: 1px solid #202020;
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 50%; 
  }


  @media (max-width: 1200px) {
    .main__img{
        transition: all 0.3s ease 0s;
        max-width: 530px;
    }
  }

  @media (max-width: 1024px) {
    
    .main__img{
        transition: all 0.3s ease 0s;
        max-width: 430px;
    }
  }

  @media (max-width: 890px) {

    .main__body{
        flex-direction: column;
        margin-top: 100px;
    }

    .main__info{
        align-items: center;
        max-width: none;
    }

    .imgContainer {
        background-color: #21181800;
      }

    .main__img{
        padding-top: 50px;
    }
  }

  @media (max-width: 768px) {
    .main__body{
        margin-top: 80px;
    }

    .main__h2{
        text-align: center;
        font-size: 28px;
    }

    .main__btn{
        flex-wrap: wrap;
        justify-content: center;
        gap: 40px;
    }
  }