.footer__container{
    padding-bottom: 30px;
    margin-top: 80px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.footer__h2{
    font-size: 48px;
    font-weight: 700;
}

.footer__ul{
    padding: 5px 10px;
    margin-top: 25px;
    display: flex;
    gap: 40px;
}


.span_border{
    position: relative;
    margin: 40px 0px;
    width: 100%;
    animation-name: animation-border;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
}

@keyframes animation-border {
     0%{
        box-shadow: 2px 0px 20px 8px rgb(45, 115, 243);
        border-bottom: 1px solid rgb(45, 115, 243);
     }
     25%{
        box-shadow: 2px 0px 20px 8px rgb(9, 62, 159);
        border-bottom: 1px solid rgb(9, 62, 159);
     }
     50%{
        box-shadow: 2px 0px 20px 8px rgb(243, 40, 104);
        border-bottom: 1px solid rgb(243, 40, 104);
     }
     75%{
        box-shadow: 2px 0px 20px 8px rgb(237, 97, 253);
        border-bottom: 1px solid rgb(237, 97, 253);
     }
     100%{
        box-shadow: 2px 0px 20px 8px rgb(172, 40, 243);
        border-bottom: 1px solid rgb(172, 40, 243);
     }
}

.span{
    margin: 15px 0px;
    color: #4F4F4F;
    font-size: 14px;
    font-weight: 500;
}

@media (max-width: 768px) {

    .footer__container{
        padding-bottom: 20px;
        margin-top: 35px;
    }

    .footer__h2{
        text-align: center;
        font-size: 25px;
    }

    .footer__ul{
        flex-wrap: wrap;
        justify-content: center;
    }

    .footer__ul svg{
        width: 40px;
        height: 40px;
    }
    
}