.works__container{
    text-align: center;
    margin-top: 120px;
}

.works__h2{
visibility: hidden;
}

.works__h2Active{
    overflow: hidden;
    width: 7ch;
    display: inline-block;
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    white-space: nowrap;
    animation: flashing-border 1s ease infinite, printed-text 5s ease
}

.swiper__container{
    margin-top: 102px;
    display: flex;
    gap: 15px;
    justify-content: space-between;
    flex-wrap: wrap;
}

.skills{
    padding-top: 133px;
}

.skills__h2{
    visibility: hidden;
    margin-bottom: 73px;
}

.skills__h2Active{
    overflow: hidden;
    width: 7ch;
    display: inline-block;
    color: #fff;
    font-size: 48px;
    font-weight: 700;
    white-space: nowrap;
    animation: flashing-border 1s ease infinite, printed-text 5s ease;
    margin-bottom: 73px;
}

.skills__wrapper{
    max-width: 1168px;
    width: 100%;
    background-color: #212121;
    margin: 0 auto;
}
 
.skills__list{
    height: 235px;
    display: flex;
    align-items: center;
    justify-content: space-around;
    gap: 30px;
}


.skills__list li {
    text-align: center;
}

.skills__mark{
    color: #4F4F4F;
    font-size: 72px;
    font-weight: 700;
    line-height: normal;
    min-width: 152px;
}

.skills__name{
    color: #27AE60;
    font-size: 24px;
    font-weight: 700;
    line-height: normal;
}

@keyframes flashing-border{
    0%{
        border-right: 4px solid #4F4F4F;
    }
    50%{
        border-right: 4px solid transparent;
    }
    100%{
        border-right: 4px solid #4F4F4F;
    }
}

@keyframes printed-text {
    from{
        width: 0%;
    }

}

@media (max-width: 1290px) {
    .swiper__container{
        transition: all 1s ease 0s;
        flex-wrap: wrap;
        justify-content: center;
    }
}

@media (max-width:950px) {
    .skills__list{
        padding: 15px 0px 15px 0px;
        height: auto;
        flex-wrap: wrap;
    }
    .skills__list li {
        width: 45%;
    }
}

@media (max-width: 768px) {

    .works__container{
        margin-top: 40px;
    }

    .works__h2Active{
        font-size: 25px;
    }

    .swiper__container{
        margin-top: 40px;
    }

    .skills{
        padding-top: 60px;
    }

    .skills__h2{
    margin-bottom: 40px;
    }

    .skills__h2Active{
        font-size: 25px;
        margin-bottom: 40px;
    }

    .skills__mark{
        min-width: auto;
        font-size: 30px;
    }
    .skills__name{
        font-size: 13px;
    }
}