.wrapper{
    background-position: center;
    min-height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url("../backroundImg/bacfroundImg.png");
}

.container{
    max-width: 1333px;
    width: 100%;
    margin: 0 auto;
    padding: 0 10px;
}

.header{
    position: fixed;
    width: 100%;
    top: -1px;
    left: 0;
    z-index: 50;
}

.header::before{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgb(255 255 255 / 7%);
    backdrop-filter: blur(4px);
    z-index: 2;
}


.nav{
    position: relative;
    display: block;
    z-index: 3;
    height: 50px;
    display: flex;
    align-items: center;
}

.certificate__h2{
    padding-top: 80px;
    text-align: center;
    font-size: 48px;
    font-weight: 700;
    color: #fff;
}

.certificate__content{
    display: flex;
    flex-direction: column;
    gap: 60px;
    padding-top: 60px;
    padding-bottom: 60px;
}

.certificate__body{
    display: flex;
    justify-content: flex-start;
    gap: 40px;
}

.certificate__img{
    position: relative;
    max-width: 500px;
}

.certificate__img img{
    width: 100%;
}

.certificate__description{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.listDescription{
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.listDescription li{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.listTitle{
    display: flex;
}

.svgTitle{
    position: relative;
    max-width: 100px;
}

.svgTitle img{
    width: 100%;
}

.descriptionList h3{
    line-height: 35px;
    color: #fff;
    font-size: 18px;
    font-weight: 700;
}

.descriptionList span{
    color: #7f7f7f;
    font-size: 15px;
    font-weight: 400;
}

.descriptionLi{
    color: #7b939e;
    font-size: 14px;
    font-weight: 600;
}

.appraisal__certificate{
    color: #54d226;
    font-size: 24px;
}

.listLink{
    display: flex;
    justify-content: space-between;
}

@media (max-width:860px) {
    .certificate__content{
        align-items: center;
    }

    .certificate__body{
        flex-direction: column;

    }

    .listTitle{
        justify-content: center;
    }

    .listLink{
        padding-top: 30px;
        justify-content: space-around;

    }
}

@media (max-width: 425px) {
    .certificate__h2{
        font-size: 28px;
        padding-top: 70px;
    }

    .certificate__content{
        padding-top: 35px;
    }

    .certificate__body{
        gap: 20px;
    }

    .certificate__img{
        max-width: 350px;
    }

    .svgTitle{
        max-width: 75px;
    }

    .listDescription h3{
        font-size: 15px;
    }
    .descriptionLi,
    .listDescription span{
        font-size: 12px;
    }

    .appraisal__certificate{
        font-size: 15px;
    }

    .listLink{
        flex-wrap: wrap;
        gap: 15px;
    }
}